import {useEffect} from 'react'
import PopularPosts from '../components/PopularPosts'
import PostItem from '../components/PostItem'
import { useSelector, useDispatch } from 'react-redux';
import Loader from './../components/loader/Loader';
import { checkIsAuth } from './../redux/slices/AuthSlice.js';
import { useNavigate } from 'react-router-dom';
import { getPopularPosts } from '../redux/slices/PostSlice.js';

const MainPage = () => {

  const {posts, popularPosts, loading} = useSelector(state => state.post)
  const dispatch = useDispatch()
  
  useEffect(() => {
    dispatch(getPopularPosts())
  }, [posts])

  const navigate = useNavigate()
  const isAuth = useSelector(checkIsAuth)
  if(!isAuth) {
    return navigate('/login')
  }

  if (loading) {
    return <Loader/>
  }
  if (!posts.length) {
    return <h2 className='text-center text-white'>Постов нет</h2>
  }

  return (
    <div className='max-w-[900px] mx-auto py-10'>
      <div className="flex justify-between gap-8">
          <div className="flex flex-col gap-10 basis-4/5">
            {posts?.map(post => <PostItem post={post} key={post?._id ? post._id : new Date()}/>)}
          </div>
          <div className='basis-1/5'>
              <div className="text-xs uppercase text-white">
                Популярное:
                {popularPosts?.map(post => <PopularPosts post={post} key={post._id}/>)}
              </div>
          </div>
      </div>
    </div>
  )
}

export default MainPage