import { createSlice, createAsyncThunk } from '@reduxjs/toolkit';
import axios from '../../utils/axios.js'
import {toast} from 'react-toastify'

const initialState = {
    posts: [],
    popularPosts: [],
    onePost: [],
    loading: false,
}

export const createPost = createAsyncThunk('post/createPost', 
    async (params) => {
        try {
            const {data} = await axios.post('/posts', params)
            return data
        } catch (e) {
            console.log(e)
        }
    }
)
export const updatePost = createAsyncThunk('post/updatePost', 
    async (updatedPost) => {
        try {
            const {data} = await axios.put(`/posts/${updatePost.id}`, updatedPost)
            return data
        } catch (e) {
            console.log(e)
        }
    }
)

export const getAllPosts = createAsyncThunk('posts/getAllPosts',
    async () => {
        try {
            const {data} = await axios.get('/posts')        
            return data
        } catch (error) {
            console.log(error)
        }
    }
)
export const getPopularPosts = createAsyncThunk('posts/getPopularPosts',
    async () => {
        try {
            const {data} = await axios.get('/posts/popularPosts')        
            return data
        } catch (error) {
            console.log(error)
        }
    }
)

export const getOnePost = createAsyncThunk('posts/getOnePost', 
    async (id) => {
        try {
            const {data} = await axios.get(`/posts/${id}`)
            return {data,id}
        } catch (error) {
            console.log(error)
        }
    }
)

export const getMyPosts = createAsyncThunk('posts/getMyPosts',
    async () => {
        try {
            const {data} = await axios.get('/posts/user/me')
            return data
        } catch (error) {
            console.log(error)
        }
    }
)

export const removePost = createAsyncThunk('posts/removePost',
    async (id) => {
        try {
            const {data} = await axios.delete(`/posts/${id}`)
            return {data,id}
        } catch (error) {
            console.log(error)
        }
    }
)
export const createComment = createAsyncThunk('posts/createComment',
    async (text,id) => {
        try {
            const {data} = await axios.post(`/comments/${id}`, text)
            return {data,id}
        } catch (error) {
            console.log(error)
        }
    }
)




const postSlice = createSlice({
    name: 'post',
    initialState,
    reducers: {

    },
    extraReducers: {
        // create post
        [createPost.pending]: (state) => {
            state.loading = true
        },
        [createPost.rejected]: (state) => {
            state.loading = false
        },
        [createPost.fulfilled]: (state, action) => {
            state.loading = false
            state.posts = [action.payload, ...state.posts]
            toast('Пост успешно создан')
        },
        // get all
        [getAllPosts.pending]: (state) => {
            state.loading = true
            state.posts = []
            state.popularPosts = []
        },
        [getAllPosts.rejected]: (state) => {
            state.loading = false
        },
        [getAllPosts.fulfilled]: (state, action) => {
            state.loading = false
            state.posts = [...action.payload.posts]
        },
        [getPopularPosts.fulfilled]: (state, action) => {
            state.popularPosts = action.payload
        },
        // get one post
        [getOnePost.pending]: (state) => {
            state.loading = true
            state.onePost = []
        },
        [getOnePost.rejected]: (state) => {
            state.loading = false
        },
        [getOnePost.fulfilled]: (state, action) => {
            state.loading = false
            state.posts = state.posts.map(post => {
                if(post._id === action.payload.id) {
                    return {...post, views: action.payload.data.data.views}
                } else return post
            })
            state.onePost = action.payload.data
        },
        // get my posts
        [getMyPosts.pending]: (state) => {
            state.loading = true
        },
        [getMyPosts.rejected]: (state) => {
            state.loading = false
        },
        [getMyPosts.fulfilled]: (state, action) => {
            state.loading = false
        },

        // delete post
        [removePost.pending]: (state) => {
            state.loading = true
        },
        [removePost.rejected]: (state) => {
            state.loading = false
        },
        [removePost.fulfilled]: (state, action) => {
            state.loading = false
            state.posts = state.posts.filter(post => post._id !== action.payload.id)
        },

        // updated post
        [updatePost.pending]: (state) => {
            state.loading = true
        },
        [updatePost.rejected]: (state) => {
            state.loading = false
        },
        [updatePost.fulfilled]: (state, action) => {
            state.loading = false
            const idx = state.posts.findIndex(post => post._id == action.payload._id)
            state.posts[idx] = action.payload
            state.onePost = action.payload
        },
    }
})

export default postSlice.reducer

