import { useParams } from 'react-router-dom'
import React, {useState} from 'react'
import { useSelector, useDispatch } from 'react-redux';
import {  updatePost } from './../redux/slices/PostSlice.js';
import {toast} from 'react-toastify'
import { useNavigate } from 'react-router-dom';


const EditPostPage = () => {
  const {id} = useParams()
  const navigate = useNavigate()
  const dispatch = useDispatch()
  const data = useSelector(state => state.post.onePost.length ?  state.post.onePost : state.post.posts.filter(post => post._id == id))[0]
  const [title, setTitle] = useState(data?.title || '')
  const [text, setText] = useState(data?.text  || '')
  const [oldImage,setOldImage] = useState(data?.imgUrl || '')
  const [newImage,setNewImage] = useState('')
  const {loading} = useSelector(state => state.post)



  const handleChange = () => {
    try {
      if (title.length > 2 && text.length > 4) {
        const updatedPost = new FormData()
        updatedPost.append('title', title)
        updatedPost.append('text', text)
        updatedPost.append('id', id)
        updatedPost.append('image', newImage || oldImage)
        dispatch(updatePost(updatedPost)).then(() => {toast('Пост успешно обновлен');navigate(`/posts/${id}`)})
        
      } if (title.length < 3) {
        toast('минимальная длинна заголовка - 3 символа')
      }
      if (text.length < 5) {
        toast('минимальная длинна текста - 5 символов')
      }
    } catch (error) {
      console.log(error)
    }
  }
  return (
    <form 
      className='w-1/3 mx-auto py-10'
      onSubmit={e => e.preventDefault()}
    >
      <label className='text-gray-300 py-2 bg-gray-600 text-xs mt-2 flex items-center justify-center border-2 border-dotted cursor-pointer'>
        Прикрепить изображение:
        <input type="file" className='hidden'  onChange={e => {
          setNewImage(e.target.files[0])
          setOldImage('')
          }
        }/>
      </label>
      <div className='flex object-cover py-2 justify-center'> 
        {oldImage && (
          <img className='text-center' src={`http://localhost:5000/${oldImage}`} alt="image" />
        )}
        {newImage && (
          <img className='text-center' src={URL.createObjectURL(newImage)} alt="image" />
        )}
      </div>
      <label className='text-xs text-white opacity-70'>
        Заголовок поста:
        <input
          type="text"
          className='mt-1 text-black w-full rounded-lg bg-gray-400 border py-1 px-2 text-xs outline-none placeholder:text-gray-700'
          placeholder='Заголовок'
          value={title}
          onChange={e => setTitle(e.target.value)}
        />
          
      </label>
      <label className='text-xs text-white opacity-70'>
        Текст поста:
        <textarea
          className='mt-1 text-black resize-none h-40 w-full rounded-lg bg-gray-400 border py-1 px-2 text-xs outline-none placeholder:text-gray-700'
          placeholder='Текст поста'
          value={text}
          onChange={e => setText(e.target.value)}
        />
      </label>
      <div className='flex gap-8 items-center justify-center mt-4'>
        <button
          disabled={loading}
          className={`flex justify-center items-center ${ loading ? 'bg-gray-400' :`bg-gray-600`} text-xs text-white rounded-md py-2 px-4 hover:bg-gray-700 transition-colors`}
          onClick={handleChange}
        >
          Изменить
        </button>
        <button onClick={() => navigate(-1)} disabled={loading} className='flex justify-center items-center bg-red-600 text-xs text-white rounded-md py-2 px-4 hover:bg-red-700 transition-colors'>
          Отменить 
        </button>

      </div>
    </form>
  )
  
}

export default EditPostPage