import { Link } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { useState,useEffect } from 'react';
import { loginUser } from '../redux/slices/AuthSlice.js';
import { toast } from 'react-toastify';
import { useNavigate } from 'react-router-dom';

const LoginPage = () => {
  const navigate = useNavigate()
  const dispatch = useDispatch()
  const [username,setUsername] = useState('')
  const [password,setPassword] = useState('')
  const auth = useSelector(state => state.auth)
  const hendlerSubmit = e => {
    e.preventDefault();
    dispatch(loginUser({username,password}))
  }

  useEffect(() => {
    if(auth.status) {
      toast(auth.status)
    }
    if(auth.user) {
      navigate('/')
    }
  }, [auth.status])

  return (
    <div className='flex justify-between items-center'>
      <div className='w-1/2 text-white text-center'>
        <h1 className='mb-5 text-lg'>Добро пожаловать!</h1>
        <p className='text-base'>Данное приложение позволяет делиться с друзьями любой информацией посредставом постов</p>
        <Link to='https://vk.com/kirillfahredinov' target='_blank' className='text-red-300 border-b'>Woniderhaf</Link>
      </div>
      <form
        className='w-1/4 h-60 mx-auto mt-40'
        onSubmit={e =>  hendlerSubmit(e)}
      >
        <h1 className="text-lg text-white text-center">Авторизация</h1>
        <label  className='text-xs text-gray-400'>
          Username:
          <input
            value={username}
            onChange={e => setUsername(e.target.value)}
            type="text"
            placeholder='Username'
            className='mt-1 text-black w-full rounded-lg bg-gray-400 border py-1 px-2 text-xs outline-none placeholder:text-gray-700'
          />
        </label>

        <label  className='text-xs text-gray-400'>
          Password:
          <input
            value={password}
            onChange={e => setPassword(e.target.value)}
            type="password"
            placeholder='Password'
            className='mt-1 text-black w-full rounded-lg bg-gray-400 border py-1 px-2 text-xs outline-none placeholder:text-gray-700'
          />
        </label>

        <div className='flex gap-8 justify-center mt-4'>
          <button className='flex justify-center items-center text-xs text-white rounded-sm py-2 px-4 bg-gray-600' type='submit'>
            Войти
          </button>
          <Link to='/register' className='flex justify-center items-center text-xs text-white '>Нет аккаунта?</Link>
        </div>
        {/* <div className='text-center mt-2 text-red-600' >
          {auth.error && auth.status}
        </div> */}
      </form>
    </div>
  )
}

export default LoginPage