import React from 'react'
import { useParams,useNavigate } from 'react-router-dom'
import Moment from 'react-moment'
import {AiFillEye} from 'react-icons/ai'
import Loader from './../components/loader/Loader';
import { useSelector,useDispatch } from 'react-redux';
import { removePost } from './../redux/slices/PostSlice.js';
import {toast} from 'react-toastify'
import { createComment, getPostComments } from '../redux/slices/CommentSlice.js';
import Comment from '../components/Comment';
import {AiOutlineUser} from 'react-icons/ai'

const PostPage = () => {
  const dispatch = useDispatch()
  const navigate = useNavigate()
  const {id} = useParams()
  const {user} = useSelector(state => state.auth)
  const {data:post,author} = useSelector(state => state.post.onePost ?  state.post.onePost : state.post.posts.filter(post => post._id == id))
  const {comments, loading} = useSelector(state => state.comment)
  const [comment,setComment] = React.useState('')
  
  const handlerDeletePost = () => {
    dispatch(removePost(id)).then(() => {toast('Пост был удален'); navigate('/')})
  }
  React.useEffect(() => {
    dispatch(getPostComments(id))
  }, [])
  const handleSubmit = e => {
    e.preventDefault()
    dispatch(createComment({id,text:comment}))
    setComment('')
  }

  if(!post) return <Loader/>
  return (
    <>
      <button onClick={() => navigate(-1)}  className='flex justify-center items-center bg-gray-600 text-xs text-white rounded-sm py-2 px-4'>
        Назад
      </button>

      <div className="flex gap-10 py-10">
        <div className="w-2/3 bg-gray-600 rounded-md h-max">
          <div className="flex flex-col basic-1/4 flex-grow">
            <div className={post.imgUrl ? 'flex rounded-sm h-[25rem]' : 'flex rounded-sm'}>
              {post?.imgUrl && (<img src={`https://mya-2.herokuapp.com/${post.imgUrl}`} alt='img' className='object-cover w-full ' />)}
            </div>
            <div className='p-5'>
              <div className='flex justify-between items-center'>
                <div className='flex items-center gap-3'>
                  {author?.avatar
                    ? <div className='w-10 h-10 overflow-hidden rounded-full'> <img className='w-full h-full object-cover' src={`http://localhost:5000/${author.avatar}`} alt="" /></div>
                    : <div className='w-10 h-10 overflow-hidden rounded-full flex items-center justify-center bg-slate-200'> <AiOutlineUser/> </div>
                  }
                     
                  <div className='text-xs text-white opacity-50'>{author?.username}</div>
                </div>
                <div className='text-xs text-white opacity-50'><Moment date={post.createdAt} format='D MMM YYYY hh:mm A'/></div>
              </div>
              <div className="text-white text-xl">{post.title}</div>
              <p className="text-white opacity-60 text-xs pt-4">{post.text}</p>
              <div className="flex items-center gap-3 mt-2">
                <button className='flex items-center justify-center gap-2 text-xs text-white opacity-50'><AiFillEye/> <span>{post.views}</span></button>
              </div>
            </div>
          </div>
        </div>
        <div className="w-1/3 bg-gray-600 rounded-sm p-8 flex flex-col justify-between h-max">
          <h2 className='text-center text-white text-lg'>комментарии</h2>
          {loading && <p className='text-center'>Загрузка...</p>}
          {comments && !loading && <div className='h-full py-2 my-2 flex flex-col gap-2'>
            {comments.map(com => <Comment key={com._id} data={com}/>)}
          </div>}
          <form onSubmit={e => handleSubmit(e)} className='flex gap-2'>
            <input type="text"
              placeholder='Ваш комментарий'
              className='text-black w-full rounded-sm bg-gray-400 border p-2 text-xs outline-none placeholder:text-gray-700'
              value={comment}
              onChange={e => setComment(e.target.value)}
            />
            <button type='submit' className='glex justify-center items-center bg-gray-800 text-xs text-white rounded-sm py-2 px-4'>Отправить</button>
            
          </form>
        </div>
      </div>
      <div className='flex gap-2'>
        { post?.author == user?._id ? <button onClick={handlerDeletePost}  className='flex justify-center items-center bg-red-600 hover:bg-red-700 transition-colors text-xs text-white rounded-sm py-2 px-4'>
          Удалить пост
        </button> : null }
        { post?.author == user?._id ? <button onClick={() => navigate(`/posts/${post._id}/edit`)}  className='flex justify-center items-center bg-zinc-600 hover:bg-zinc-700 transition-colors text-xs text-white rounded-sm py-2 px-4'>
          Изменить пост
        </button> : null }
      </div>

    </>
  )
}

export default PostPage