import React from 'react'
import Moment from 'react-moment'
const Comment = ({data}) => {
  return (
    <div className='w-full border-b-[1px] pb-2'>
      <div className='text-xs'>{data.user && data.user}</div>
      <div className='flex justify-between items-center'>
        <strong className='text-white'>{data.text}</strong>
        <p className='text-xs text-gray-400'><Moment date={data.createdAt} format='D MMM YYYY hh:mm A'/></p>
      </div>

  </div >
  )
}

export default Comment