import {Route, Routes, useNavigate} from 'react-router-dom'
import Layout from './components/Layout';
import * as pages from './pages/';
import { useEffect } from 'react';
import {ToastContainer} from 'react-toastify'
import { useDispatch, useSelector } from 'react-redux';
import { getMe } from './redux/slices/AuthSlice.js';
import { getAllPosts } from './redux/slices/PostSlice';
import Loader from './components/loader/Loader';
import {checkIsAuth} from './redux/slices/AuthSlice.js'

function App() {
  const navigate = useNavigate()
  const isAuth = useSelector(checkIsAuth)
  const dispatch = useDispatch()

  useEffect(() => {
    dispatch(getMe())
    dispatch(getAllPosts())
    if (!isAuth) {
      return navigate('/login')
    }
  }, [isAuth])

  const {isLoading} = useSelector(state => state.auth)
  if (isLoading) {
    return <Loader/>
  }

  return (
    <div className="">
      <Layout>
        <Routes>
          <Route path='/' element={<pages.MainPage/>}/>
          <Route path='posts' element={<pages.PostsPage/>}/>
          <Route path='posts/:id' element={<pages.PostPage/>}/>
          <Route path='posts/:id/edit' element={<pages.EditPostPage/>}/>
          <Route path='new' element={<pages.AddPostPage/>}/>
          <Route path='login' element={<pages.LoginPage/>}/>
          <Route path='register' element={<pages.RegisterPage/>}/>

        </Routes>
        <ToastContainer position='bottom-right'/>
      </Layout>
    </div>
  );
}

export default App
