import React from 'react'
import { Link } from 'react-router-dom'
import { AiFillEye} from 'react-icons/ai'
import { getOnePost } from './../redux/slices/PostSlice';
import { useDispatch } from 'react-redux';

const PopularPosts = ({post}) => {
  const dispatch = useDispatch()
  return (
    <div className='bg-gray-600 my-1'>
        <Link to={`/posts/${post._id}`} onClick={() => dispatch(getOnePost(post._id))} className="flex justify-between text-xs text-gray-300 p-2 hover:bg-gray-800 hover:text-white">
          {post.title}
          <p className='flex items-center gap-1'>{post.views}<AiFillEye/></p>
        </Link>
        
    </div>
  )
}

export default PopularPosts