import React from 'react'
import {AiFillEye, AiOutlineMessage} from 'react-icons/ai'
import Moment from 'react-moment'
import { Link } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { getOnePost } from '../redux/slices/PostSlice.js';
const PostItem = ({post}) => {
  const dispatch = useDispatch() 
  return (
    <Link to={`/posts/${post._id}`} onClick={() => dispatch(getOnePost(post._id))}>
      <div className='flex flex-col basis-1/4 flex-grow shadow-md hover:shadow-xl rounded overflow-hidden bg-gray-700'>
        <div className={post.imgUrl ? 'flex rounded-sm h-80' : 'flex rounded-sm'}>
          {post.imgUrl && (<img src={`https://mya-2.herokuapp.com/${post.imgUrl}`} alt='img' className='object-cover w-full' />)}
          {/* {post?.imgUrl && (<img src={`http://localhost:5000/${post.imgUrl}`} alt='img' className='object-cover w-full' />)} */}
        </div>
        <div className='p-5'>
          <div className='flex justify-between items-center'>
            <div className='text-xs text-white opacity-50'>{post.username}</div>
            <div className='text-xs text-white opacity-50'><Moment date={post.createdAt} format='D MMM YYYY hh:mm A'/></div>
          </div>
          <div  className="text-white text-xl">{post.title}</div>
          <p className="text-white opacity-60 text-xs pt-4">{post?.text.length > 100 ? `${post.text.slice(0,100)}...` : post.text}</p>
          <div className="flex items-center gap-3 mt-2">
            <button className='flex items-center justify-center gap-2 text-xs text-white opacity-50'><AiFillEye/> <span>{post.views}</span></button>
            <button className='flex items-center justify-center gap-2 text-xs text-white opacity-50'><AiOutlineMessage/> <span>{post.comments?.length || 0}</span></button>
          </div>
        </div>
      </div>
    </Link>
  )
}

export default PostItem