import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import axios from '../../utils/axios.js'
const initialState = {
    comments: [],
    loading: false
}

export const createComment = createAsyncThunk('comment/createComment', async(params) => {
    try {
        const {data} = await axios.post(`/comments/${params.id}`, params)
        return data
    } catch (error) {
        
    }
})
export const getPostComments = createAsyncThunk('comment/getPostComments', async(id) => {
    try {
        const {data} = await axios.get(`/comments/${id}`)
        return data
    } catch (error) {
        
    }
})



const CommentSlice = createSlice({
    name: 'comment',
    initialState,
    reducers: {

    },
    extraReducers: {
        [createComment.rejected]: (state) => {
            state.loading = false
        },
        [createComment.fulfilled]: (state, action) => {
            state.comments = [...state.comments, action.payload]
            state.loading = false
        },
        [getPostComments.pending]: (state) => {
            state.loading = true
        },
        [getPostComments.rejected]: (state) => {
            state.loading = false
        },
        [getPostComments.fulfilled]: (state, action) => {
            state.loading = false
            state.comments = action.payload
        },
    }
})


export default CommentSlice.reducer