import React from 'react'
import {Link, NavLink} from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux';
import {loguot} from '../redux/slices/AuthSlice.js'
import {toast} from 'react-toastify'
const Navbar = () => {

  const dispatch = useDispatch()
  const {user, token} = useSelector(state => state.auth)
  const isAuth = !!token || false
  const isActive = ({isActive}) => isActive ? {color: 'white', borderBottom: '1px solid white'} : undefined

  const Logout = () => {
    dispatch(loguot())
    toast('Вы вышли из системы')
  }
  return (
    <div className='flex py-4 justify-between items-center'>
      <span className='flex justify-center items-center w-6 h-6 bg-gray-600 text-xs text-white rounded-sm'>E</span>
      { isAuth &&  // только когда авторизованы видим меню
        
        <ul className="flex gap-8">
          <li><NavLink style={isActive} to="/" className='text-xs text-gray-400 hover:text-white transition-colors'>Главная</NavLink></li>
          <li><NavLink style={isActive} to="/posts" end className='text-xs text-gray-400 hover:text-white transition-colors'>Мои посты</NavLink></li>
          <li><NavLink style={isActive} to="/new" className='text-xs text-gray-400 hover:text-white transition-colors'>Добавить пост</NavLink></li>
        </ul>
        
      }
      <div className='flex gap-4 items-center'>
        <div className='flex items-center gap-3'>
        <div className='w-10 h-10 rounded-full overflow-hidden'>{user?.avatar && <img className='object-cover w-full h-full' src={`https://mya-2.herokuapp.com/${user.avatar}`}/>}</div>
        <p className='text-white'>{user?.username}</p>
        </div>
        <Link to={isAuth ? '' : '/login'} onClick={() => isAuth && Logout()} className='flex justify-center items-center bg-gray-600 text-xs text-white rounded-sm px-4 py-2 cursor-pointer hover:bg-white transition-colors hover:text-gray-600'>
          { isAuth 
            ? 'Выйти'
            : 'Войти'
          }
        </Link>
      </div>
    </div>
  )
}

export default Navbar