import React from 'react'
import PostItem from '../components/PostItem'
import { useDispatch, useSelector } from 'react-redux';
import { getMyPosts } from '../redux/slices/PostSlice';
import Loader from './../components/loader/Loader';

const PostsPage = () => {
  const [posts, setPosts] = React.useState(null)
  const {loading} = useSelector(state => state.post)
  const dispatch = useDispatch() 
  React.useEffect(() => {
    dispatch(getMyPosts()).then(res => setPosts(res.payload))
  }, [])

  if(loading) {
    return <Loader/>
  }

  if(!posts?.length) {
    return <h2 className='text-center text-white'>Постов нет</h2>
  }
  
  return (
    <div className='max-w-[900px] mx-auto py-10'>
      <div className="flex justify-center gap-8">
        <div className="flex flex-col gap-10 w-full">
          {posts?.map(post => <PostItem post={post} key={post?._id ? post._id : new Date()} full/>)}
        </div>
      </div>
  </div>
  )
}

export default PostsPage