import { useState } from 'react';
import { Link,useNavigate } from 'react-router-dom';
import { registerUser } from '../redux/slices/AuthSlice.js';
import {useDispatch, useSelector} from 'react-redux'
import { useEffect } from 'react';
import {toast} from 'react-toastify'
import {MdOutlinePhotoCamera} from 'react-icons/md'

import 'react-toastify/dist/ReactToastify.css'
const RegisterPage = () => {
  const navigate = useNavigate()
  const [username,setUsername] = useState('')
  const [password,setPassword] = useState('')
  const [image,setImage] = useState('')
  const dispatch = useDispatch()
  const auth = useSelector(state => state.auth)

  const handleSubmit = e => {
    e.preventDefault();
    const data = new FormData()
    data.append('username', username)
    data.append('password',password)
    data.append('image',image)
    dispatch(registerUser(data))
  }
  useEffect(() => {
    if(auth.status) {
      toast(auth.status)
    }
    if (auth.token) {
      return navigate('/')
    }

  }, [auth])


  return (
    <form
      className='w-1/4 h-60 mx-auto mt-40'
      onSubmit={e =>  handleSubmit(e)}
    >
      <h1 className="text-lg text-white text-center">Регистрация</h1>
      <div className='flex justify-center mt-5'>
        <label className='cursor-pointer'>
          <input type="file" className='hidden' onChange={e => setImage(e.target.files[0])}/>
          <div className='w-16 h-16 bg-slate-200 object-cover flex items-center justify-center rounded-full overflow-hidden'>
            {image ? <img src={URL.createObjectURL(image)} alt=""  className='object-cover h-full w-full'/> : <MdOutlinePhotoCamera/>}
          </div>
        </label>
      </div>
      
      <label  className='text-xs text-gray-400'>
        Username:
        <input
          value={username}
          onChange={e => setUsername(e.target.value)}
          type="text"
          placeholder={`${auth.error ? auth.status : 'Username' }`}
          className='mt-1 text-black w-full rounded-lg bg-gray-400 border py-1 px-2 text-xs outline-noneplaceholder:text-gray-700'
         />
      </label>

      <label  className='text-xs text-gray-400'>
        Password:
        <input
          value={password}
          onChange={e => setPassword(e.target.value)}
          type="password"
          placeholder='Password'
          className='mt-1 text-black w-full rounded-lg bg-gray-400 border py-1 px-2 text-xs outline-none placeholder:text-gray-700'
        />
      </label>

      <div className='flex gap-8 justify-center mt-4'>
        <button disabled={auth.isLoading} className={`flex justify-center items-center text-xs text-white rounded-sm py-2 px-4 ${auth.isLoading ? 'bg-gray-900' :'bg-gray-600'}`} type='submit'>
          Зарегестрироваться
        </button>
        <Link to='/login' className='flex justify-center items-center text-xs text-white '>Уже зарегистрированы?</Link>
      </div>

      {/* <div className='text-center mt-2 text-red-600' >
        {auth.error && auth.status}
      </div> */}
    </form>
  )
}




export default RegisterPage